// 导入 axios 实例
import service from "..";

// 获取歌手详情
export function getArtistDetail(id, cookie) {
  return service({
    method: 'GET',
    url: `/artist/detail?id=${id}&cookie=${cookie}`
  })
}
// 获取歌手专辑
export function getArtistAlbum(id, cookie, albumSize) {
  return service({
    method: 'GET',
    url: `/artist/album?id=${id}&cookie=${cookie}&limit=${albumSize}`
  })
}
// 获取歌手 mv，根据数量请求
export function getArtistMv(id, cookie, mvSize) {
  return service({
    method: 'GET',
    url: `/artist/mv?id=${id}&cookie=${cookie}&limit=${mvSize}`
  })
}
// 获取歌手描述
export function getArtistDesc(id, cookie) {
  return service({
    method: 'GET',
    url: `/artist/desc?id=${id}&cookie=${cookie}`
  })
}
// 获取相似歌手
export function getArtistSimi(id, cookie) {
  return service({
    method: 'GET',
    url: `/simi/artist?id=${id}&cookie=${cookie}`
  })
}
// 歌手热门 50 首歌曲
export function getArtistTopSong(id, cookie) {
  return service({
    method: 'GET',
    url: `/artist/top/song?id=${id}&cookie=${cookie}`
  })
}