import { getArtistDetail } from '@/request/api/singerPage';
import { getMvDetail, getMvDetailInfo, getCommentMv, getMvAdd, getVideoDetail, getCommentVideo, getVideoAdd } from '@/request/api/videoPlayer';
import { postComment } from '@/request/api/songInfo';
export default {
  data() {
    return {
      // 页面类型，1是 Mv，2是视频
      type: 0,
      // 歌手 id
      artistId: 0,
      // 歌手详情
      artistDetail: {// artist: { cover: '' } 改为可选链 ?.
      },
      // mv的 id
      mvid: 0,
      // 视频的 id(字符串形式)
      videoid: '',
      // mv 播放地址
      mvUrl: '',
      // 视频播放地址
      videoUrl: '',
      // mv 数据
      mvInfo: {},
      // 视频数据
      videoInfo: {// creator: { nickname: '' } 改为可选链 ?.
      },
      // mv 点赞转发评论数
      mvNums: {},
      // 是否展开简介
      isDesc: 0,
      // 最大评论文字数
      maxlength: 140,
      // 评论剩余可输入长度
      leftover: 140,
      // 评论内容
      commentContent: '',
      // 评论总数
      totalNum: 0,
      // 精彩评论
      hotComments: [],
      // 显示的精彩评论
      tenHotComments: [],
      // 最新评论
      latestComments: [],
      // 是否显示“更多精彩评论”
      isTab: 0
    };
  },

  mounted() {
    // 页面刷新而路由也不会丢失 id(在路由地址中拼接在末尾)
    this.mvid = this.$route.query.mvid || 0;
    this.videoid = this.$route.query.videoid || 0; // 1是 Mv，2是视频

    this.type = this.$route.query.type; // console.log(this.mvid, this.videoid, this.type);

    if (sessionStorage.getItem("cookie")) {
      if (this.type == 1) {
        // 获取 mv 播放地址
        this.getMvAddress(); // 获取 mv 数据

        this.getMvDetail(); // 获取 mv 点赞转发评论数数据

        this.getMvDetailInfo(); // 获取 mv 评论

        this.getCommentMv();
      } else {
        // 获取视频播放地址
        this.getVideoAdd(); // 获取视频详情

        this.getVideoDetail(); // 获取视频评论

        this.getCommentVideo();
      }
    }
  },

  methods: {
    // 获取 mv 数据
    async getMvDetail() {
      const res = await getMvDetail(this.mvid, sessionStorage.getItem("cookie")); // console.log('获取 mv 数据', res);

      this.mvInfo = res.data.data; // console.log('mv 数据', this.mvInfo);

      this.artistId = this.mvInfo.artistId; // 获取歌手详情

      this.getArtistDetail();
    },

    // 获取 mv 点赞转发评论数数据
    async getMvDetailInfo() {
      const res = await getMvDetailInfo(this.mvid, sessionStorage.getItem("cookie")); // console.log('获取 mv 点赞转发评论数', res);

      this.mvNums = res.data; // console.log('mv 点赞转发评论数', this.mvNums);
      // 评论数

      this.totalNum = this.mvNums.commentCount;
    },

    // 获取 mv 评论
    async getCommentMv() {
      const res = await getCommentMv(this.mvid, sessionStorage.getItem("cookie")); // console.log('获取 mv 评论', res);

      this.totalNum = res.data.total;
      this.hotComments = res.data.hotComments; // 精彩评论如果超过十条则取前十显示，否则全取，isTab 需在 tenHotComments 之前赋值

      this.isTab = this.hotComments.length > 10 ? 1 : 0;
      this.tenHotComments = this.hotComments.length > 10 ? this.hotComments.splice(0, 10) : this.hotComments;
      this.latestComments = res.data.comments; // console.log('mv的精彩评论', this.tenHotComments);
      // console.log('mv的最新评论', this.latestComments);
    },

    // 获取视频评论
    async getCommentVideo() {
      const res = await getCommentVideo(this.videoid, sessionStorage.getItem("cookie")); // console.log('获取视频评论', res);

      this.totalNum = res.data.total;
      this.hotComments = res.data.hotComments; // 精彩评论如果超过十条则取前十显示，否则全取，isTab 需在 tenHotComments 之前赋值

      this.isTab = this.hotComments.length > 10 ? 1 : 0;
      this.tenHotComments = this.hotComments.length > 10 ? this.hotComments.splice(0, 10) : this.hotComments;
      this.latestComments = res.data.comments; // console.log('视频的精彩评论', this.tenHotComments);
      // console.log('视频的最新评论', this.latestComments);
    },

    // 获取 mv 播放地址
    async getMvAddress() {
      // 分辨率，默认 1080
      const res = await getMvAdd(this.mvid, sessionStorage.getItem("cookie")); // console.log('获取 mv 播放地址', res);

      this.mvUrl = res.data.data.url; // console.log('mv播放地址', this.mvUrl);
    },

    // 获取视频详情
    async getVideoDetail() {
      const res = await getVideoDetail(this.videoid, sessionStorage.getItem("cookie")); // console.log('获取视频详情', res);

      this.videoInfo = res.data.data; // console.log('视频详情', this.videoInfo);
    },

    // 获取视频播放地址
    async getVideoAdd() {
      const res = await getVideoAdd(this.videoid, sessionStorage.getItem("cookie")); // console.log('获取视频播放地址', res);

      this.videoUrl = res.data.urls[0].url; // console.log('视频播放地址', this.videoUrl);
    },

    // 获取歌手详情
    async getArtistDetail() {
      const res = await getArtistDetail(this.artistId, sessionStorage.getItem("cookie")); // console.log('获取歌手详情', res);

      this.artistDetail = res.data.data; // console.log('歌手详情', this.artistDetail);
    },

    // 时间戳转日期
    formatPublishTime(time) {
      return this.$formatDate(time);
    },

    // 鼠标位于某个评论处
    mouseenter(index, type) {
      if (type === 1) this.$refs.report[index].style.display = 'block';
      if (type === 2) this.$refs.latestReport[index].style.display = 'block';
    },

    // 鼠标移出所有评论
    mouseleave(type) {
      if (type === 1) {
        for (let i = 0; i < this.$refs.report.length; i++) {
          this.$refs.report[i].style.display = 'none';
        }
      } else {
        for (let i = 0; i < this.$refs.latestReport.length; i++) {
          this.$refs.latestReport[i].style.display = 'none';
        }
      }
    },

    // 鼠标移入某个点赞
    zanMouseenter(index, type) {
      if (type === 1) {
        // this.$refs.zanIcon 与 this.$refs.zanNum 数组长度一致
        for (let i = 0; i < this.$refs.zanIcon.length; i++) {
          this.$refs.zanIcon[i].style.fill = '#545454';
          this.$refs.zanNum[i].style.color = '#a7a5a5';
        }

        this.$refs.zanIcon[index].style.fill = '#000';
        this.$refs.zanNum[index].style.color = '#000';
      } else {
        for (let i = 0; i < this.$refs.latestZanIcon.length; i++) {
          this.$refs.latestZanIcon[i].style.fill = '#545454';
          this.$refs.latestZanNum[i].style.color = '#a7a5a5';
        }

        this.$refs.latestZanIcon[index].style.fill = '#000';
        this.$refs.latestZanNum[index].style.color = '#000';
      }
    },

    // 鼠标移出所有点赞
    zanMouseleave(type) {
      if (type == 1) {
        for (let i = 0; i < this.$refs.zanIcon.length; i++) {
          this.$refs.zanIcon[i].style.fill = '#545454';
          this.$refs.zanNum[i].style.color = '#a7a5a5';
        }
      } else {
        for (let i = 0; i < this.$refs.latestZanIcon.length; i++) {
          this.$refs.latestZanIcon[i].style.fill = '#545454';
          this.$refs.latestZanNum[i].style.color = '#a7a5a5';
        }
      }
    },

    // 去往个人主页
    gotoPersonalHome(userId) {
      this.$router.push({
        path: '/PersonalHome',
        query: {
          userId: userId
        }
      });
    },

    // 评论发布时间的处理
    publishTime(time, timeStr) {
      // 检查是否昨天发布的
      // 定义正则查找规则，test() 如果字符串中包含规则则返回 true 否则 false
      const reg = /昨天/; // if (timeStr.indexOf('昨天') != -1)

      if (reg.test(timeStr)) {
        // console.log('昨天发布的：', timeStr);
        // '昨天'与时间中间加空格处理，字符串截取 slice() 包前不包后，左闭右开
        let newStr = timeStr.slice(0, 2) + ' ' + timeStr.slice(2, timeStr.length); // console.log(newStr);

        return newStr;
      } else {
        // 获取当前年份
        let YNow = new Date().getFullYear(); // 把返回的时间戳转为时间格式

        let date = new Date(time);
        let Y = date.getFullYear();
        let M = date.getMonth() + 1;
        let D = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        let H = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
        let Min = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes(); // 今年发布的评论不需显示年份

        if (Y === YNow) return M + '月' + D + '日 ' + H + Min;else return Y + '年' + M + '月' + D + '日 ' + H + Min;
      }
    },

    // 去往更多精彩评论页(携带歌单 id)
    goMoreHotComments() {
      this.$router.push({
        path: '/HotComments',
        query: {
          id: this.mvid,
          // 类型 1 为歌单的更多精彩评论，2 为专辑，3 为歌曲， 4 为 Mv
          type: 4
        }
      });
    },

    // 对数量的处理
    changeCount(num) {
      return this.$formatCount(num);
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    },

    // 点击作者头像
    goHeadCreater() {
      // 去往歌手页
      if (this.type == 1) this.goSingerPage(this.artistId); // 去往个人主页

      if (this.type == 2) this.gotoPersonalHome(this.videoInfo.creator.userId);
    },

    // 发表评论
    async postComment() {
      // query 1是 Mv，2是视频，commentType 1是 Mv，5是视频
      var commentType = this.$route.query.type == 1 ? 1 : 5;
      var commentId = this.$route.query.type == 1 ? this.mvid : this.videoid; // console.log(commentType, commentId);
      // 生成当前时间的时间戳

      var timestamp = new Date().getTime(); // console.log(this.commentContent, this.commentContent.length, timestamp);

      const res = await postComment(1, commentType, commentId, this.commentContent, timestamp, sessionStorage.getItem("cookie")); // console.log('发送MV/视频评论', res);
      // console.log('发送MV/视频评论', res.data.comment);

      alert('评论成功！');
    }

  },
  watch: {
    // 评论剩余可输入长度随着输入内容增多而减短
    commentContent() {
      this.leftover = 140 - this.commentContent.length;
    }

  }
};